import React from 'react';
import Media from 'react-media';


import Dimage1 from '../images/desktop/image1.jpg';
import Dimage2 from '../images/desktop/image2.jpg';
import Dimage3 from '../images/desktop/image3.jpg';
import Dimage4 from '../images/desktop/image4.jpg';
import Dimage5 from '../images/desktop/image5.jpg';

import Mimage1 from '../images/mobile/image1.jpg';
import Mimage2 from '../images/mobile/image2.jpg';
import Mimage3 from '../images/mobile/image3.jpg';
import Mimage4 from '../images/mobile/image4.jpg';
import Mimage5 from '../images/mobile/image5.jpg';



const Image1 = () => {
    return(
      <Media query = {{maxWidth: 768}}>
        {matches =>
          matches ? (<img src={Mimage1} alt="image1" className='image'></img>
        ) :(<img src={Dimage1} alt="image1" className='image'></img>)}
        </Media>
    );
  }


